<template>
  <div class="test-container">
    <h1>{{ testTitle }}</h1>
    <div v-for="(question, index) in questions" :key="index">
      <h3>{{ question.text }}</h3>
      <div class="answers-container">
        <button
          v-for="(answer, aIndex) in question.answers"
          :key="aIndex"
          :class="{ selected: answer.selected, correct: answer.isCorrect && answer.selected, incorrect: !answer.isCorrect && answer.selected }"
          @click="selectAnswer(question, answer)"
        >
          {{ answer.text }}
        </button>
      </div>
    </div>
    <div v-if="score !== null">
      <p>Puntuación: {{ score }} / {{ questions.length }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      testTitle: 'Test Example', // Esto lo cambiaremos para cada test
      questions: [],             // Contendrá las preguntas cargadas
      score: null,               // La puntuación del usuario
      incorrectAnswers: 0        // Contador de respuestas incorrectas
    };
  },
  created() {
    this.loadTestData();
  },
  methods: {
    async loadTestData() {
      const testId = this.$route.params.id; // Obtener el ID del test desde la URL
      const response = await fetch(`/tests/test-${testId}.json`);
      const data = await response.json();

      this.testTitle = data.title;
      // Mezclar preguntas
      this.questions = data.questions.sort(() => Math.random() - 0.5);

      // Mezclar respuestas de cada pregunta
      this.questions.forEach(question => {
        question.answers.sort(() => Math.random() - 0.5);
        question.wrongAnswered = false; // Inicializa como no respondida incorrectamente
        question.answers.forEach(answer => {
          answer.selected = false; // Inicializa como no seleccionado
        });
      });
    },
    selectAnswer(question, answer) {
      // Actualiza el estado de la respuesta seleccionada
      question.answers.forEach(a => {
        a.selected = false; // Desmarca todas las respuestas
      });
      answer.selected = true; // Marca la respuesta seleccionada

      // Verifica si la respuesta es correcta
      if (answer.isCorrect) {
        // Si la pregunta ya fue respondida incorrectamente, no se suma el punto
        if (!question.wrongAnswered) {
          if (this.score === null) this.score = 0;
          this.score += 1;
        }
      } else {
        // Incrementar el contador de respuestas incorrectas
        this.incorrectAnswers += 1;
        question.wrongAnswered = true; // Marca que esta pregunta ha sido respondida incorrectamente

        // Restar un punto por cada dos respuestas incorrectas
        if (this.incorrectAnswers === 2) {
          this.score = this.score > 0 ? this.score - 1 : 0; // Asegura que la puntuación no sea negativa
          this.incorrectAnswers = 0; // Reinicia el contador
        }
      }
    }
  }
};
</script>

<style scoped>
.test-container {
  max-width: 600px; /* Ajusta el ancho máximo según tus necesidades */
  margin: 0 auto; /* Centra el contenedor horizontalmente */
  text-align: left; /* Alinea el texto a la izquierda */
}

h1, h3 {
  color: #333;
}

.answers-container {
  display: flex;
  flex-direction: column; /* Coloca los botones uno debajo del otro */
}

button {
  margin: 5px;
  padding: 10px;
  border: none; /* Elimina el borde por defecto */
  background-color: #007bff; /* Color de fondo del botón */
  color: white; /* Color del texto del botón */
  border-radius: 5px; /* Bordes redondeados */
  cursor: pointer; /* Cambia el cursor al pasar por encima */
  transition: background-color 0.3s; /* Transición suave */
}

button:hover {
  background-color: #0056b3; /* Color de fondo al pasar el ratón */
}

/* Estilo para respuesta seleccionada */
button.selected {
  background-color: #28a745; /* Color verde para la respuesta seleccionada */
}

/* Estilo para respuesta correcta */
button.correct {
  background-color: #28a745; /* Color verde para la respuesta correcta */
}

/* Estilo para respuesta incorrecta */
button.incorrect {
  background-color: #dc3545; /* Color rojo para la respuesta incorrecta */
}
</style>
